import { useContext, useEffect } from "react";
import { useParams } from "react-router"
import context from "../../context/context";
import { type } from "@testing-library/user-event/dist/type";
import CircularIndeterminate from "../loading/loading";
import LineChart from "./lineChart";

function CoinPage (){
    const {id} = useParams()
    const {data, dispatch} = useContext(context)

    useEffect(()=>{        
        fetchCoinData()
        fetchHistoricalData()
    },[data.currentValue])


   function fetchCoinData (){
        const options = {
            method: 'GET',
            headers: {accept: 'application/json', 'x-cg-demo-api-key': 'CG-WjyiQpKNRKFEAJmPmzrAV6DA'}
          };
          
          fetch('https://api.coingecko.com/api/v3/coins/'+id, options)
            .then(res => res.json())
            .then(res => dispatch({type: 'getCoin', payload: res}))
            .catch(err => dispatch({type: 'coinPageError', payload: err}));
    }
    
    function fetchHistoricalData(){
        const options = {
            method: 'GET',
            headers: {accept: 'application/json', 'x-cg-demo-api-key': 'CG-WjyiQpKNRKFEAJmPmzrAV6DA'}
          };
          
          fetch(`https://api.coingecko.com/api/v3/coins/${id}/market_chart?vs_currency=${data.currentValue.value}&days=10&interval=daily`, options)
            .then(res => res.json())
            .then(res => dispatch({type: 'getHistorical', payload: res}))
            .catch(err => dispatch({type: 'coinPageError', payload: err}));
    }
   
    if(data.coinPage.loading){
        return <CircularIndeterminate />
    }else{
        console.log(data.coinPage.data);
        
    }
    
    return(
        <div className="container">
            
            {
                data.coinPage.loading?<CircularIndeterminate />:
                data.coinPage.error? <h3>{data.coinPage.error.message}</h3>:
                <>
                <div className="title">
                    <div >
                        <h3>{id + ' '+ data.coinPage.data?.symbol.toUpperCase()}</h3>
                        <img src={data.coinPage.data?.image.thumb} alt="" />
                    </div>
                    
                    <p>{data.coinPage.data?.description.en.slice(0,data.coinPage.data.description.en.indexOf('.',300)+1)}</p>
                </div>
                <div>
                    
                </div>
                <div>
                    
                    <div className="liveChart">
                        {data.coinPage.historicalData&&!data.coinPage.error&&<LineChart />}
                    </div>
                </div>
                </>
               
            }
            
        </div>
    )
}
export default CoinPage