import { useContext, useEffect, useRef, useState } from "react";
import context from "../../context/context";
import Loading from "../loading/loading";
import CircularIndeterminate from "../loading/loading";
import SearchIcon from '@mui/icons-material/Search';
import ArrowDropUpRoundedIcon from '@mui/icons-material/ArrowDropUpRounded';
import ArrowDropDownRoundedIcon from '@mui/icons-material/ArrowDropDownRounded';
import Pagination from '@mui/material/Pagination';
import Stack from '@mui/material/Stack';
import { Link } from "react-router-dom";
import { type } from "@testing-library/user-event/dist/type";


function CoinTable(){
    const {data, dispatch} = useContext(context)
    const [filterList, setFilterList] = useState([])
    const inputRef = useRef('')
    
     function fetchCoins  (){
        const options = {method: 'GET', headers: {accept: 'application/json', 'x-cg-demo-api-key': 'CG-WjyiQpKNRKFEAJmPmzrAV6DA'}, };
        fetch('https://api.coingecko.com/api/v3/coins/markets?vs_currency=' + data.currentValue.value, options)
          .then(response => response.json())
          .then(response => dispatch({type: 'getData', payload: response}))
          .catch(err => dispatch({type: 'error', payload: err}));  
    }
    
    useEffect(()=>{
        fetchCoins ()
        dispatch({type: 'cancel', payload: true})

    },[data.currentValue])
    useEffect(()=>{
        search()
    },[data.coinList])

    

  function search(){
        if(inputRef.current.value){
            setFilterList(data.coinList?.filter(val=>val.name.slice(0, inputRef.current.value.length).toLowerCase()===inputRef.current.value.toLowerCase()))    
        }else{
            setFilterList(data.coinList.slice(0, 20))
            
        }
  }
  function changePage(e, page){
    setFilterList(data.coinList.slice(page*20-20, page*20))
    e.target.style.background='grey !important'
    
  }
  
 
    return(
        <div className="container">
            <div className="inputDiv">
                <input placeholder="Search Coin" type="text" ref={inputRef}  onChange={search}/>
                <SearchIcon className="searchIcon" />
            </div>
            {
                data.loading? <CircularIndeterminate />:
                data.error? <h3>{data.error}</h3>:
                filterList.length?
                <>
                <table>
                <thead>
                    <tr>
                    <th>#</th>
                    <th>Name</th>
                    <th>Price</th>
                    <th>24H</th>
                    <th>Market Cap</th>
                    </tr>
                    
                </thead>
                <tbody>
                    {
                        
                    filterList.map((coin, i)=>(
                            <tr key={i}  className='list'>
                                <td>{coin.market_cap_rank}</td>
                                <td>
                                <Link to={'/coin/'+coin.id}>
                                    <div>
                                    
                                    <img src={coin.image} alt="" />
                                    {coin.name+' '} 
                                    {coin.symbol.toUpperCase()}
                                    
                                    
                                    </div>
                                    </Link>
                                    </td>
                                <td>{data.currentValue.symbol+coin.current_price.toLocaleString()}</td>
                                <td className={coin.price_change_percentage_24h>0?'positive':'negative'}>
                                    <div className="priceChange">
                                        {coin.price_change_percentage_24h>0?<><ArrowDropUpRoundedIcon/> +</>:<ArrowDropDownRoundedIcon />}
                                        {Math.floor(coin.price_change_percentage_24h*100)/100}%
                                    </div>
                                </td>
                                <td>{data.currentValue.symbol+coin.market_cap.toLocaleString()}</td>
                                
                            </tr>
                        ))
                    }
                </tbody>
            </table>
             <div className="pagination">
             <Stack spacing={2}>
                <a href="#"><Pagination color="primary"  onChange={changePage} count={Math.floor(filterList.length/4)}  shape="rounded" /></a>
                 
             </Stack>
             </div>
                </>
            
        :
        <h3 className="errorText">No Result</h3>
            }
           
            
            </div>
    )
}
export default CoinTable

