import { useContext } from "react"
import Chart from "react-google-charts"
import context from "../../context/context"

function LineChart (){
    const {data} = useContext(context)
    return(
        <Chart  
        chartType="Line"
        width="100%"
        height="350px"
        data={data.coinPage.historicalData} />
    )
}
export default LineChart