import logo from './logo.svg';
import './App.css';
import { useEffect, useReducer, useRef, useState } from 'react';
import Header from './component/header/header';
import Search from './component/search/search';
import CoinTable from './component/coinTable/coinTable';
import { state, reducer } from './reducer/reducer';
import context from './context/context';
import { createBrowserRouter, Route, Routes } from 'react-router-dom';
import CoinPage from './component/coinPage/coinPage';

function App() {
  const [data, dispatch] = useReducer(reducer, state)
  
  
  return (
    <context.Provider value={{data, dispatch}}>
      <Header />
      <Routes>
        <Route path='/' element={<CoinTable />} />
        <Route path='/coin/:id' element={<CoinPage/>} />
      </Routes>
      
    </context.Provider>
  );
}

export default App;
