import CoinPage from "../component/coinPage/coinPage"

export const state = {
    loading: true,
    error: '',
    currentValue: {
        value: 'usd',
        symbol: '$'
    },
    coinList: [],
    coinPage: {
        loading: true,
        error: '',
        data: null,
        historicalData: null,
    }
}

export const reducer = (state, action) => {
    if (action.type === 'changeVal'){
        switch(action.value){
            case 'USD':
                return {
                    ...state,
                    currentValue: {
                       value: 'usd',
                        symbol: '$'
                    }
                }
                break
            case 'EUR':
                return {
                    ...state,
                    currentValue: {
                       value:'eur', 
                       symbol: '€'
                    }
                }
                break
            case 'RUB':
                return {
                    ...state,
                    currentValue: {
                       value:'rub', 
                       symbol: '₽'
                    }
                }
        }
    }else if(action.type === 'getData'){
        return {
            ...state,
            loading: false,
            error: '',
            coinList: action.payload
        }
    }else if(action.type ==='error'){
        return{
            ...state,
            loading: false,
            error: action.payload
        }
    }else if(action.type === 'getCoin'){
        console.log('ok');
        
                return{
            ...state,
            coinPage: {
                ...state.coinPage,
                error: '',
                data: action.payload,
                loading: false,
            }
        }
    }else if(action.type==='coinPageError'){
        return{
            ...state,
            coinPage: {
                ...state.coinPage,
                loading: false,
                error: action.payload,
            }
        }
    }else if(action.type==='getHistorical'){
        let historicalDataCopy = [["Date","Prices"]]
        console.log(action.payload);
        
        action.payload.prices.map((item)=>{
            historicalDataCopy.push([`${new Date(item[0]).toLocaleDateString().slice(0,-5)}`, item[1]])
        })
        return{
            ...state,
            coinPage: {
               ...state.coinPage,
               historicalData: historicalDataCopy,
              
            }
        }
    }else if(action.type==='cancel'){
        console.log('ok');
        
        return{
            ...state,
            coinPage: {
               ...state.coinPage,
               loading: action.payload,
            }
        }
    }
    return state
}
